//  useState
import { useState, useEffect } from 'react';

import { useSearchParams } from 'react-router-dom';

//   
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//          api
import { getMoviesByNameAndPage } from '../utils/api';

//  
import Title from '../components/Title/Title';
import Searchbar from '../components/Searchbar/Searchbar';
import ListOfMovies from 'components/ListOfMovies/ListOfMovies';
import Loader from 'components/Loader/Loader';
import LoadMoreButton from '../components/LoadMoreButton/LoadMoreButton';

//   
import { Container } from '../components/App.styled';

//   
const Movies = () => {
  // 
  // moviesArray -   
  // query -    
  // showModal -   
  // page -  ,   1
  // showBtnLoadMore -     
  // isLoading -   
  const [moviesArray, setMoviesArray] = useState([]);
  const [query, setQuery] = useState('');
  const [page, setPage] = useState(1);
  const [showBtnLoadMore, setShowBtnLoadMore] = useState(false);
  const [showLoader, setShowLoader] = useState(false);

  const [searchParams, setSearchParams] = useSearchParams();
  const movieName = searchParams.get('query');

  //       
  useEffect(() => {
    //   ,   
    if (!movieName) {
      return;
    }
    setQuery(movieName);
    setShowLoader(true);
    getMoviesByNameAndPage(movieName, 1)
      .then(data => {
        setMoviesArray([...data.results]);
        const alreadyDownloaded = 20 * 1;
        if (alreadyDownloaded < data.total_results) {
          setShowBtnLoadMore(true);
        }
      })
      .finally(() => {
        setShowLoader(false);
      });
  }, [movieName]);

  //         SUBMIT  
  //  -,   moviesArray     page  1
  //       query
  //           
  //      query,   toFind  
  const onSubmitSearchBtn = toFind => {
    if (toFind === query) {
      toast(`     : "${toFind}"!`);
      return;
    }
    setMoviesArray([]);
    setPage(1);
    setQuery(toFind);
    getFromAPI(toFind, 1);
    setSearchParams({ query: toFind });
  };

  //    LOAD_MORE
  //             1  
  //     + 1
  const loadMoreMovies = () => {
    getFromAPI(query, page + 1);
    setPage(prevPage => prevPage + 1);
  };

  // ,          
  //   ,  (?),   ,     
  const getFromAPI = (toFind, page) => {
    setShowLoader(true);
    //  
    getMoviesByNameAndPage(toFind, page)
      .then(data => {
        // console.log('data', data);
        //    ,     
        //       (query, page, showBtnLoadMore),   
        if (data.total_results < 1) {
          toast.error(`  "${toFind}"  !`);
          setPage(1);
          setQuery('');
          setShowBtnLoadMore(false);
        }
        //      ,  
        //  ()   moviesArray  
        else if (data.total_results !== 0) {
          setMoviesArray(prevMoviesArray => [
            ...prevMoviesArray,
            ...data.results,
          ]);
          //
          //      .
          //     " " ( showBtnLoadMore)
          //            
          //        
          //     ,      data.total_results
          const alreadyDownloaded = 20 * page;
          if (alreadyDownloaded < data.total_results) {
            //  ,     
            if (page === 1) {
              toast(
                `  "${toFind}"  : ${data.total_results}.  " ",    20 !`
              );
            }
            // ,      
            else {
              const moreMovies = data.total_results - alreadyDownloaded;
              toast(
                `  "${toFind}"   : ${moreMovies}  ${data.total_results}.  " ",    20 !`
              );
            }
            //      ,     
            setShowBtnLoadMore(true);
          }
          //        ,         LoadMore
          else {
            toast(
              `     "${toFind}".   !`
            );
            setShowBtnLoadMore(false);
          }
        }
      })
      // .catch(e => {
      // setError(e.message);
      // toast.error(e.message);
      // console.log('error', error);
      // })
      .finally(() => {
        setShowLoader(false);
      });
  };

  return (
    <>
      {showLoader && <Loader />}
      <ToastContainer autoClose={2000} />
      <section>
        <Container>
          <Title text="Search movies" />
          <Searchbar onSubmit={onSubmitSearchBtn} />
          {moviesArray.length !== 0 && (
            <ListOfMovies trendingMovies={moviesArray} />
          )}

          {showBtnLoadMore && (
            <LoadMoreButton loadMoreMovies={loadMoreMovies} />
          )}
        </Container>
      </section>
    </>
  );
};

export default Movies;
